.precios {
    width: 60%;
    height: 60%;
    scroll-behavior: smooth;
}

.crypto-icon {
    border-radius: 50%;
    vertical-align: middle;
    margin-right: 10px;
    height:50px;
    width: 50px;
}

.table {
    margin-top: 1rem;
}

.table th {
    font-weight: 600;
}

.table td {
    vertical-align: middle;
}

.crypto-wrapper {
    display: flex;
    gap: 1rem;
}

.list-column {
    transition: transform 300ms ease;
}

.list-column.shifted {
    transform: translateX(-20%);
}

.chart-column {
    transition: opacity 300ms ease, transform 300ms ease;
}

.list-column tr.active {
    background: rgba(0,123,255,0.06);
}

.chart-container {
    background: #fff;
    border: 1px solid rgba(0,0,0,0.05);
    padding: 8px;
    border-radius: 6px;
}

@media (max-width: 767px) {
    .list-column.shifted { transform: none; }
}

/* TradingView loading overlay */
.tv-loading {
  position: absolute;
  inset: 0;
  background: rgba(255,255,255,0.7);
  z-index: 10;
}

.tv-loading .spinner-border {
  width: 3rem;
  height: 3rem;
}
